@keyframes incognito{
	0%{}
	65%{opacity: 0.6}
	67%{opacity: 0.5}
	70%{opacity: 0.3}
	72%{opacity: 0.2}
	74%{opacity: 0.1}
	75%{opacity: 0}
	100%{opacity: 0}
}

@keyframes heart{
	0%{transform: scale(1);}
	25%{transform: scale(1.25);}
	50%{transform: scale(1);}
	75%{transform: scale(1.25);}
	100%{transform: scale(1);}
}
/*start svg animation*/
@keyframes position-1{
	0%{}
	100%{}
}
@keyframes position-2{
	0%{}
	100%{}
}
@keyframes position-3{
0%,100%{left: 15%;top: 20.5%;}
50%{left: 15%;top: 16%;}
}
@keyframes position-4{
0%,100%{left: 65%;top: 20.5%;}
37.5%{top: 18%;transform:rotate(40deg);}
75%{left: 65%;top: 20%;transform:rotate(-35deg);}
}
@keyframes position-5{
0%,100%{}
50%{transform: rotate(20deg);}
75%{transform: rotate(90deg);}
}
@keyframes position-6{
0%,100%{}
50%{transform: rotateZ(180deg);}
75%{transform: rotateY(90deg);}
}
@keyframes position-7{
0%{}
50%{left:74%;top:35%;transform: rotateZ(50deg);}
75%{left:79%;top:23%;transform: rotateZ(230deg);}
100%{}
}
@keyframes position-8{
0%{}
25%{left:35%;top:37%;transform: rotate(90deg);}
50%{left:50%;top:40%;transform: rotate(145deg);}
75%{left:62%;top:43%;}
100%{left:62%;top:56.5%;}
}
/*end svg animation*/