/* #Blog posts*/

.blog{
	padding: $section-top;
	&__text{
	color: #4caf50;
	font-size: 1.8325vw;
	font-weight: 450;
	i.fa.fa-pencil::before {
    margin-right: .91625vw;
}
}
	&__p{
	margin:$paragraph-top / 2.5 0 $paragraph-bottom / 2;
    font-size: 2vw;
    font-weight: bold;
}
	a{
    padding: 1.1728vw 3.665vw 1.1728vw 1.466vw;
    font-weight: 400;
    vertical-align: sub;
    font-size: 1.4058vw;
}
    &__btn:hover{
    @include boxShadow($box-shadow-style-1);
}
&__card{
	padding: .733vw 1.466vw 1.466vw;
	margin-top: $section-top;
	cursor: pointer;
	@include borderRad(.2932vw);
	@include transition(.2s);
	&:hover{
	@include boxShadow($box-shadow-style);		
}
	&__title{
	font-weight: bold;
	font-size: 2.265vw;
	margin-bottom: $heading-bottom;
}
	&__p{
	line-height: 1.4;
	font-size: 1.4058vw;
	font-weight: 100;
	margin:$paragraph-top / 2 0 $paragraph-bottom / 6;
}
    span{
    font-size: 1.3194vw;
}
	a{
	font-size: 1.3194vw;
    border-bottom: 2px dashed #999;
    padding: 0 0px .2199vw;
    &:first-of-type{
    vertical-align: initial;
    margin: 0 .3665vw;
}
}
}
}