/* #Default theme */
/********************EXAMPLE OF WORK WITH THEMES**********/
/*
//Structure in HTML
-BLOCK BLOCK--MODIFIER
--BLOCK__ELEMENT
--BLOCK__ELEMENT
--BLOCK__ELEMENT

//Structure in CSS

//specify for color

//dark theme ==> is the custom theme and it will be used in dark mode
.BLOCK{
  &.BLOCK--BLOCK-MODIFIER{
    & .BLOCK__ELEMENT{
		color:blue;
		background-color: silver;
	}
	& .BLOCK__ELEMENT{
		color:red;
		background-color: silver;
	}
}}

//light theme ===> is the default theme and it will be used in light mode
.BLOCK{
  &.BLOCK--BLOCK-MODIFIER{
    & .BLOCK__ELEMENT{
		color:crimson;
		background-color: orangered;
	}
	& .BLOCK__ELEMENT{
		color:cornflowerblue;
		background-color: orangered;
	}
}}
//specify for other property
.BLOCK{
	&__ELEMENT,&__ELEMENT,&__ELEMENT{
		font-size: 30px;
		margin-left: 30px;
		margin-top: 50px;
	}
    &__ELEMENT{
    	font-size: 25px;
    	display: block;
    }
}
*/
/*
In essence:I have set these structures to show you how to do a change of mode,
 all you have to do is work with the dark theme structure only,
 because the light theme is the original or default theme.
-Dark theme will be used in dark mode.
-Light theme will be used in light mode.
 */

 /*my HTML structure
  color-scheme color-scheme--dark-mode
  color-scheme__ELEMENT
  color-scheme__ELEMENT
  color-scheme__ELEMENT
  color-scheme__ELEMENT
  color-scheme__ELEMENT
  color-scheme__ELEMENT
  color-scheme__ELEMENT
 */
.color-scheme--dark-mode{
	background:#232323;
	.navigation{
        a{color: lighten($nav-color, 70%);}
            &__item,&__list,&__icon{
                background: #232323;
            }
        &__list{
        	box-shadow: 0px 0px 17px #0000004d;
        }
        &__icon{
        	border-right: 2px solid #00000014;
            box-shadow: -25px 0px 25px #00000026;        	&__button{
        		&__x{
        			&:before,&:after{
        				background: lighten($nav-color, 70%);
        			}
        		}
        	}
        }
    }
	.header{
		background:darken($bg, 30%);
		&__intro{
		 &__text{
				color:darken(white, 30%);
			}
		 &__btn-1{border-color: darken($bg, 15%); 
		 	      color: darken(white, 15%);
		 	      background: darken($bg, 15%);}
		 &__btn-2{border-color: darken($white, 15%); 
		 	      color: #333;
		 	      background: darken($white, 15%);}
		}
	}
	.blog-header{
	    background: darken($bg, 30%);
	    &__content{
	     &__text{color: lighten(#212121, 30%);}
		 p{color: lighten(#212121, 60%);}
	    }
	}
	.main-blog{
	 .cards{
	  .blog__card{background: darken(#212121, 1%);
        &__title{color: lighten(#212121, 20%);}
		&__p{color: lighten(#212121, 60%);}
		}
		}
	}
	.main-docs{
		span{background-color: rgba(108, 109, 111, 0.42);}
        .introduction,
        .ask,
        .feature,
        .start,
        .dev,
        .support{
        h1{color: lighten(#212121, 30%);}
        h2{color: lighten(#212121, 35%);}
		p{color: lighten(#212121, 70%);}
		ul,ol{color: lighten(#212121, 60%);}
        }
	}
	.feature{
		&__text{color: lighten(#212121, 30%);}
		&__p{color: lighten(#212121, 60%);}
	}
	.technology{
		&__card{
		 background: darken(#212121, 1%);
		 h2{color: lighten(#212121, 40%);}
		 p{color: lighten(#212121, 60%);}
			}
	}
	.plugin{
		&__text{color: lighten(#212121, 30%);}
		&__p{color: lighten(#212121, 60%);}
	}
	.blog{
        &__text{color: darken(#4caf50, 10%);}
		&__p{color: lighten(#212121, 30%);}
		&__card{
			background: darken(#212121, 1%);
        &__title{color: lighten(#212121, 20%);}
		&__p{color: lighten(#212121, 60%);}
		}
	}
	.review{
		&__text{color: lighten(#212121, 30%);}
		&__p{color: lighten(#212121, 60%);}
	}
	.footer{
		background: darken($footer, 10%);
		&__p{color: lighten(#212121, 70%);}
	}
.dark-btn{border-color: darken($bg, 15%); 
		 	  color: darken(white, 15%);
	          background: darken($bg, 15%);}
.light-btn{border-color: darken($white, 15%); 
		       color: #333;
		 	   background: darken($white, 15%);}
i.fa.fa-long-arrow-right{
		color: darken(#4caf50, 3%);}
.a{stroke: darken(#ff9e4a, 15%);}
.d{stroke: darken(#c22dd2, 15%);}
.g{stroke: darken(#1cd987, 15%);}
.j{stroke: darken(#ffffff, 15%);}
.o{fill: darken(#dc1d37, 15%);}
.q{fill: darken(#1976d2, 15%);}
.r{fill: darken(#dc1934, 15%);}
}