/* #Technologies*/

.technology{
	margin-top: 1.466vw;
	padding: $section-padding;
	user-select: none;
	&__card {
	@include borderRad(.2932vw);
    @include boxShadow($box-shadow-style-2);
    @include transition(.2s);
    margin-bottom: 3.665vw;
    cursor: pointer;
    &:hover{
    transform:translateY(-.2199vw);
     @include boxShadow($box-shadow-style);}
	img{
	padding-top: .733vw;
	position: relative;
    left: 50%;
    transform: translate(-50%, .733vw);}
	h2{
	font-size: 1.5325vw;
    font-weight: bold;
	text-align: center;
	margin-bottom: $heading-bottom;
}
	p{
	font-size: 1.4058vw;
    padding: 0 1.8325vw $paragraph-bottom; 
    line-height: 1.5;
    
}
}
}