/* #Header */

.header{
	height: $header-height;
	background: $bg;
	background-image: $bg-linear;
	background-size: cover;
	background-position: top;
    //clip-path: polygon(0 0, 100% 0,100% 100% , 0 calc(#{$header-height} / 100 * 67.5));
    transform: skewY(6deg);
    transform-origin: 100%;
    @include transition(.4s);


    &__intro{
    transform: skewY(-6deg) translateY($header-height / 2 * 1.0563);
    img{
	width:250px;
}
    &__text{
	color:$white;
	font-size: 3.2985vw;
	margin-top: calc(#{$heading-top} + 0.7333vw);
	margin-bottom: $heading-bottom / 3 * 4;
    &::selection{
    background: #272727;
}
}
    button, a {
	user-select: none;
}
    &__btn-1{
	font-size: 1.3194vw;
    &--uppercase{
    text-transform: uppercase;
}
}
    &__btn-2{
	font-size: 1.3194vw;
	padding-left: 1.466vw;
	margin-left: 1.466vw;
	&:hover{
	text-decoration: none;
	-webkit-transition: none !important;
    -moz-transition: none !important;
    -ms-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
    -webkit-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -o-transform: none !important;
    transform: none !important;
}
    &--uppercase{
    text-transform: uppercase;
}
    i.fa.fa-github.fa-2x{
	margin-right: .733vw;
	vertical-align: sub;
}
}
}
}
/*start svg*/
/*for positioning*/
svg{   
   display: none;
   position: absolute;
   z-index: -1;
   left: 40%;top: 45%; //40 45
//first positioning
/*hash = 8oz4SZB9Li*/
   transition:all 1.5s ease;
   &:first-of-type{left: 90%;top: 1%;z-index: 996;display: none;}
   &:nth-of-type(2){left: 90%;top: 1%;z-index: 997;}
   &:nth-of-type(3){@include svg-3;}
   &:nth-of-type(4){@include svg-4;}
   &:nth-of-type(5){@include svg-5;}
   &:nth-of-type(6){@include svg-6;}
   &:nth-of-type(7){@include svg-7;}
   &:last-of-type{@include svg-8;}
    }
