/* #Plugins*/

.plugin{
	padding: $section-padding;
	text-align: center;
	&__text{
	font-size: 2.932vw;
	font-weight: bold;
    i.fa.fa-plug::before {
    margin-right: 0.91625vw;
}
}
	&__p{
	margin:$paragraph-top / 2 0 $paragraph-bottom / 2;
    font-size: 1.4058vw;
    font-weight: 450;
}
	img{
	display: inline-table;
	margin-top: 1.466vw;
	@include boxShadow($box-shadow-style-1);
    @include transition(.2s);
    cursor: pointer;
    &:hover{
    transform:translateY(-.299vw);
    @include boxShadow($box-shadow-style-3);
}
}
}