/* #Sass Variables */
//dimension
$header-height: 75vh;
$blog-hight: calc(75vh / 100 * 80);
$hero-height: 60vh;
$footer-height: 50px;
$to-top-btn-x: 45px;
$to-top-btn-y: $to-top-btn-x;
$section-padding: 8vh 0;
$section-top-extra: 130px;
$section-top: 10vh;
//box shadow
$box-shadow-style: 0px 6px 20px $box-shadow-color;
$box-shadow-style-1: 1px 1px 9px $box-shadow-color-1;
$box-shadow-style-2: 0 3px 10px -1px $box-shadow-color-2;
$box-shadow-style-3: 0 10px 20px $box-shadow-color-3;