/* #Sass Mixins */
@mixin boxShadow($box-shadow-style) {
    box-shadow: $box-shadow-style;
}

@mixin HRule {
    border: 1.8px solid $bg;
    width: 80px;
    border-radius: 50px;
    margin: auto;
    margin-top: 15px;
}

@mixin fontLato {
    font-family: 'Lato', sans-serif;
    font-weight: 400;
}

@mixin fontRoboto {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
}

@mixin fontButton {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 16px;
}

@mixin borderRadius($r) {
    -webkit-border-radius: $r;
    -moz-border-radius: $r;
    -o-border-radius: $r;
    -ms-border-radius: $r;
    border-radius: $r;
}

@mixin Border($b) {
    border: $b;
}

@mixin borderRad($br) {
    border-radius: $br;
}

@mixin U-Select($value) {
    -webkit-user-select: $value;
    -moz-user-select: $value;
    -ms-user-select: $value;
    user-select: $value; // disable selecting from user
}

@mixin transition($t) {
    transition: all $t linear;
}

@mixin Ptransition($t, $r) {
    transition: all $t $r;
}

@mixin count{
        animation-name: counter;
        animation-duration: 3s;
        animation-iteration-count: 1;
        animation-delay: 0s;
        animation-fill-mode: forwards;
}

@mixin disguise{
        animation-name: incognito;
        animation-duration: .666667s;
        animation-iteration-count: 1;
        animation-delay: 3s;
        animation-fill-mode: forwards;
}

@mixin black{
        animation-name: preloader;
        animation-duration: 1s;
        animation-iteration-count: 1;
        animation-delay: 3s;
        animation-fill-mode: forwards;
}

@mixin white{
        animation-name: preloader;
        animation-duration: .666667s;
        animation-iteration-count: 1;
        animation-delay: 3.25s;
        animation-fill-mode: forwards;
}

@mixin heart {
    animation: heart 1s infinite;
}

