@mixin animation {
    animation-name: moveInleft;
    animation-duration: 1s;
    animation-delay: 2s;
    /*to solve glitch problem*/
    -webkit-backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1.0, 1.0);
    /*glitch problem solved*/
}

@mixin animation-1 {
    animation-name: moveInright;
    animation-duration: 1s;
    animation-delay: 2s;
    /*to solve glitch problem*/
    -webkit-backface-visibility: hidden;
    -webkit-transform: translateZ(0) scale(1.0, 1.0);
    /*glitch problem solved*/
}

@mixin animation-2 {
