/* #Buttons */
.light-btn {
    @include borderRadius(50px);
    @include Border(2px solid $white);
    @include fontButton;
    @include boxShadow($box-shadow-style);
    @include transition(.2s);
    background: $white;
    color: #115566;
    padding: 1.0995vw 2.199vw;
    margin: 1.466vw 0;
    cursor: pointer;

    &:hover {
        @include boxShadow($box-shadow-style-1);
        color: #115566;
        transform: translateY(-3px);
    }

    &:active {
        @include boxShadow($box-shadow-style-2);
        transform: translateY(3px);
        outline: 0;
    }

    &:visited {}

    &:link {}

    &:focus {
        color: #115566;
        outline: 0;
    }
}

.dark-btn {
    @include borderRadius(50px);
    @include Border(2px solid $bg);
    @include fontButton;
    @include boxShadow($box-shadow-style);
    @include transition(.2s);
    background: $bg;
    color: $white;
    padding: 15px 30px;
    margin: 20px 0;
    cursor: pointer;

    &:hover {
        @include boxShadow($box-shadow-style-1);
        color: $white;
        transform: translateY(-3px);
    }

    &:active {
        @include boxShadow($box-shadow-style-2);
        transform: translateY(3px);
        outline: 0;
    }

    &:visited {}

    &:link {}

    &:focus {
        color: $white;
        outline: 0;
    }
}