/* #Footer */

.footer{
	padding: $section-padding;
	height:$footer-height;
	background:$footer;
	background-image: $footer-linear;
	background-size: cover;
	background-position: top;
	color: $white;
	letter-spacing: 1px;
	display: flex;
	justify-content: center;
	align-items: center;
	&__p{
	font-size: 1.4058vw;
    font-weight: 450;
    i.fa.fa-heart{
    	@include heart;
    	color:#dd2e44;
    	margin: 0 7.5px;
    }
	}
