/////Start layouts
/*start preloader*/
@import 'layout/preloader';
/*end preloader*/
/*start navigation*/
.navigation{
	position: fixed;
	z-index: 995;
    height: 100vh;
	//width: 38vw;
    //background:$white;
    a{
       color: $nav-color;
       font-weight: 600;
       letter-spacing: .5px;
        }
    &__checkbox{
       position: absolute;
       display: none;}    
    &__checkbox:checked + &__item{
       width: 16.5vw;
    }
    &__checkbox:checked ~ &__list{
       width: 20vw;
    }
            ///22 try : failed
    &__checkbox:checked ~ &__icon > &__icon__button > &__icon__button__x:before{
       transform: rotate(45deg);
       top: 1.5rem;}
    &__checkbox:checked ~ &__icon > &__icon__button > &__icon__button__x:after{
       transform: rotate(-45deg);
       width: 3.6rem;
       bottom: 1.5rem;
     		     }
            ///try number 23 / 24 : succed;
    //ul{li{a{text-decoration: none;}}}
    &__item,&__list,&__icon{
       display: inline-block;
       float: left;
       text-align: center;
       height: 100vh;
       background: $nav-bg;
    @include Ptransition(.2s, linear);
       overflow: hidden;
    	ul{
           position: relative;
           top: 50%;
           left: 50%;
           transform: translate(-50%, -50%);
           float: inherit;
    	}
    }
	&__item,&__list{width:0vw;

    ul{
    	li:not(:last-of-type){
    		margin-bottom: 40px;
}}
	}
	&__item{
		a{font-size: 20px;}
	}
	&__list{
		a{font-size: 16px;}
         box-shadow: 0px 0px 17px #eeeeee73;
	}
	&__icon{width: 5vw;
		box-shadow: -8px 0px 25px #3a34341c;
		border-right: 2px solid #6b6a6a12;
     &__button{
     	position:relative;
     	top: 50%;
     	transform: translateY(-50%);
     	color: red;
     	cursor: pointer;
        height: 3.6rem;
        width: 3.6rem;
     &:hover > &__x:after{
    	width: 3.6rem;
    }
    
      &__x{
      	&:before,&:after{
      		content: "";
      		position: absolute;
      		height: .5rem;
      		background: $nav-color;
      		border-radius: 50px;
      		right: 0%;
      		cursor: pointer;
      		transition: width .2s ease-out;
      	}
      	&:before{
      		width: 3.6rem;
      	    top: 1rem;
      		}
      	&:after{
      		width: 2.4rem;
      	    bottom: 1rem;
      	}
      }	
     }
	}
}
