/*start media query*/
@media only screen and (min-device-width: 320px) and (max-device-width: 425px){
.navigation__icon {
    width: 10vw;}	
   .header{
    &__intro{ transform: skewY(-6deg) translate($header-height / 7.5,$header-height / 2 * .8);}}
   .navigation__item ul li:not(:last-of-type), .navigation__list ul li:not(:last-of-type) {
    margin-bottom: 60px;
}
.blog__card__title {
    font-size: 2.6vw;   
}
}
@media only screen and (min-device-width: 320px) and (max-device-width: 768px){

svg{
        &:first-of-type{display: block;width: 260px;left: 73%}
        &:nth-of-type(2){display: block;width: 260px;left: 73%;}}
.feature{
  &__p{text-align: center;}
}
}
@media only screen and (min-device-width: 321px) and (max-device-width: 375px) {
	
