/* #Blog specific styles*/

.blog-header{
	background: darken($bg, 10%);
	height:$hero-height;
	&__content{
	transform: translateY($hero-height / 3.333);
	&__text{
	color:$white;
	margin-top: $heading-top / 6 * 5;
	font-weight: 550;
}
	&__p{
	color:$white;
	margin-top: $paragraph-top / 3;
	font-size: 1.8vw;
	font-weight: 500;
}
	&__img{}
}
}
.main-blog{
	padding: $section-padding;
	.cards{margin-top:-$section-top * 2.5; 
	.blog__card{
	background: #eeeff1;
}
}
}