/* #docs specific styles*/

.main-docs{
float: right;
span{
    background-color: rgba(27,31,35,.05);
    padding: 3px 7px;
    margin: 0 5px;
    border-radius: 2px;
}
.introduction,
.ask,
.feature,
.start,
.dev,
.support{
    padding:$section-top / 2.5;
    h1,h2{
    font-size: 2vw;
    font-weight: bold;
    margin: 30px 0;}
    p{
    font-size: 1.4058vw;
    padding: 0 0;
    line-height: 1.6;}
    ul,ol{
    font-size: 1.3058vw;
    line-height: 2;
    }
}
.introduction{padding-top: $section-top-extra;}
.ask{}
.feature{}
.start{}
.dev{}
.support{padding-bottom: $section-top-extra;}
}