/* #Preloader */

.preloader{
	&--black,&--white{
		width: 100vw;
		height: 100vh;
		position: fixed;
	}
	&--black{		
		background: #212121;		
		z-index: 999;
		color: white;
		text-align: center;
		font-size: 2.2vw;
		letter-spacing: 1px;
		span{
			// i used span for vertical positioning text
        position: absolute;
        top: 50%;
        left: 48%;
        transform: translate(-48%, -50%);
        @include disguise;
		&:after{
	  	content:'1%';
	  	margin-left: 5px;
	  	@include count;
	    }
		}
	@include black;
	}
	&--white{
		background: $white;
        z-index: 998;
    @include white;
	}
}